<template>
	<div id="isearch">
		<public-header :title="searchTitle"></public-header>
		<div class="content" v-if="condition">
			<van-cell-group>
				<template v-for="(searchItem, searchIndex) in condition.input" v-if="condition.input">
					<van-field
						v-model="condition.input[searchIndex].value"
						:label="searchItem.title"
						:placeholder="searchItem.placeholder ? searchItem.placeholder : '点击输入'"
						input-align="right"
					>
					</van-field>
				</template>
				<template v-for="(searchItem,searchIndex) in condition.list">
					<van-cell>
						<div slot="title">{{searchItem.title}}</div>
						<div slot="right-icon" class="c-gray" @click="onTogglePopup(searchItem.name)">
							{{searchItem.selected ? searchItem.selected.title: '点击选择'}}
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
				</template>
				<template v-if="condition.time">
					<van-cell>
						<div slot="title" class="">{{condition.time.start}}</div>
						<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('start')">
							{{this.time.start ? this.time.start: '点击选择'}}
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title" class="">{{condition.time.end}}</div>
						<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('end')">
							{{this.time.end ? this.time.end: '点击选择'}}
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
				</template>
			</van-cell-group>
			<van-row class="padding-10">
				<van-col span="12" class="padding-10">
					<van-button size="normal" block @click="onSearchCancel">重置</van-button>
				</van-col>
				<van-col span="12" class="padding-10">
					<van-button size="normal" block type="danger" @click="onSearchConfirm">搜索</van-button>
				</van-col>
			</van-row>
			<template v-for="(searchItem, searchIndex) in condition.list">
				<van-popup v-model="popupStatus[searchItem.name]" position="bottom">
					<div class="popup-title flex-lr van-hairline--bottom">
						<span class="c-gray" @click="onTogglePopup(searchItem.name)">取消</span>
						<span>{{searchItem.title}}</span>
						<span class="c-primary"></span>
					</div>
					<div class="popup-content">
						<van-radio-group v-model="searchItem.selected">
							<van-cell-group>
								<van-cell :title="item.title" clickable @click="onChooseOption(searchIndex, index)" v-for="(item, index) in searchItem.options" :key="index">
									<template #right-icon>
										<van-radio :name="item" />
									</template>
								</van-cell>
							</van-cell-group>
						</van-radio-group>
					</div>
				</van-popup>
			</template>
			<!--时间弹出层-->
			<van-popup class="popup-time" v-model="datePicker.status" position="bottom">
				<van-datetime-picker
					v-model="time.currentDate"
					type="date"
					:min-date="time.minDate"
					:max-date="time.maxDate"
					@confirm="onConfirmTime"
					@cancel="onToggleDatePicker"
				/>
			</van-popup>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header";
	import {mapState, mapMutations} from 'vuex';
	import {getData} from "../../../../controller/funcCommon";
	export default {
		data() {
			return {
				searchTitle: '搜索订单',
				condition: {},
				showPreLoading: true,
				datePicker: {
					minDate: new Date(2016, 1),
					maxDate: new Date(),
					currentDate: new Date(),
					status: false
				},
				time: {
					typeInput: 'start',
					minDate: new Date(2017, 12, 1),
					maxDate: new Date(),
					currentDate: new Date(),
				},
				popupStatus: {}
			}
		},
		components: {
			publicHeader
		},
		methods: {
			...mapMutations([
				'replaceState'
			]),

			onToggleDatePicker(type) {
				this.datePicker.status = !this.datePicker.status;
				if(type) {
					this.time.typeInput = type;
				}
			},

			onConfirmTime(value) {
				let time = this.util.formatDate(value, 'date');
				this.time[this.time.typeInput] = time;
				this.onToggleDatePicker();
			},

			onChooseOption(searchIndex, index) {
				var searchItem = this.condition['list'][searchIndex];
				var selected = this.condition['list'][searchIndex]['options'][index];
				this.condition['list'][searchIndex]['selected'] = selected;
				this.onTogglePopup(searchItem.name);
				this.condition = Object.assign({}, this.condition);
			},

			onSearchConfirm() {
				var data = this.onGetFilter();
				if(data.num == 0) {
					this.util.$toast('请至少填写一项搜索条件');
					return false;
				}
				this.filter = data;
				this.replaceState({ key: 'erranderStoreOrderSearch', val: this.filter});
				let url = this.util.getUrl({path: '/plugin/pages/paotui/store/order/searchResult'});
				this.$router.push(url);
			},

			onSearchCancel() {
				this.onInit(true);
			},

			onGetFilter() {
				var data = {
					items: {},
					num: 0
				};
				for(let key in this.condition.list) {
					let item = this.condition.list[key];
					if(this.condition.time && (item.name == this.condition.time.name) && (this.condition.list[this.condition.time.name]['selected']['iscustom'] == 1)) {
						data['items'][this.condition.time.name] = item.selected.value;
						data['items'][this.condition.time.key] = this.time;
					} else {
						if(item.name && item.selected) {
							data['items'][item.name] = item.selected.value;
						}
					}
					if(item['selected']['value'] != item['defaultSelected']['value']) {
						data['num']++;
					}
				}
				for(let key in this.condition.input) {
					let item = this.condition.input[key];
					if(item['value'] && item['value'] != "undefined" && item['value'] != item['defaultValue']) {
						data['items'][key] = item['value'];
						data['num']++;
					}
				}
				if(this.condition.time && this.time.start && this.time.end) {
					data['items'][this.condition.time['name']] = {
						start: this.time.start,
						end: this.time.end
					};
					data['num']++;
				}
				return data;
			},

			onInit(force) {
				for(let key in this.condition.list) {
					//页面初始化的时候执行
					if(!force) {
						this.condition.list[key]['defaultSelected'] = this.condition.list[key]['options'][0];
						if(!this.condition.list[key]['selected']) {
							this.condition.list[key]['selected'] = this.condition.list[key]['options'][0];
						}
						if (this.condition.list[key].type && this.condition.list[key].type == 'time') {
							let timekey =  this.condition.list[key].key || 'addtime';
							let itemSelected = this.condition.list[key]['selected'];
							if(!itemSelected || (itemSelected && !itemSelected[timekey])) {
								itemSelected[timekey] = {
									start: this.util.formatDate(new Date()),
									end: this.util.formatDate(new Date()),
								};
							}
							this.condition.time = {
								name: this.condition.list[key].name,
								key: timekey
							};
							this.time['start'] = itemSelected[timekey]['start'];
							this.time['end'] = itemSelected[timekey]['end'];
						}
					} else {
						this.condition.list[key]['selected'] = this.condition.list[key]['defaultSelected'];
					}
				}

				for(let key in this.condition.input) {
					if(!force) {
						this.condition.input[key]['defaultValue'] = this.condition.input[key]['value'] ? this.condition.input[key]['value'] : '';
					} else {
						this.condition.input[key]['value'] = this.condition.input[key]['defaultValue'];
					}
				}

				if(this.condition.time) {
					this.time.start = '';
					this.time.end = '';
				}
				this.condition = Object.assign({}, this.condition);
			},

			onTogglePopup(type) {
				this.popupStatus[type] = !this.popupStatus[type];
				this.popupStatus = Object.assign({}, this.popupStatus);
			},

			onLoad() {
				let that = this;
				getData({
					vue: that,
					url: 'errander/storeOrder/search',
					data: {},
					success: function(res) {
						that.condition = res.filter;
						that.onInit();
					}
				});
			}
		},

		computed: {
			...mapState([
				'erranderStoreOrderSearch',
			]),
		},

		mounted() {
			this.onLoad()
		}
	}
</script>

<style>
	#isearch .van-field .van-cell__title{
		max-width: 50%;
		width: 50%;
	}
	#isearch .van-icon-arrow{
		font-size: 12px;
		line-height: 24px;
		margin-left: 5px;
	}
	#isearch .van-popup .popup-title{
		height: 44px;
		padding: 15px;
	}
	#isearch .van-popup .popup-content{
		height: 300px;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#isearch .van-popup.popup-time .van-picker__cancel{
		color: #7a7a7a;
	}
</style>


